body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  height: 75vh;
  min-height: 10vh;
  color: #17408B;
}

.home-links {
    width: 50%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    list-style: None;
    justify-content: space-around;
    color:black !important;
    align-content: center;
  }

.nav_Style {
    color: #5c677d;
    font-weight: bold;
}

img {
    width:  100px;
    height: 100px;
    object-fit: scale-down;
}


.container {
  text-align: center;
  flex-direction: column;
  display:flex;
  align-items:center;
  justify-content:center;
}

.text {
  letter-spacing:0;
  text-align:center;
  padding:0.8em 1.6em;
  border:1px solid currentColor;
  border-radius:2px;
  background:grey;
  text-transform:capitalize;
  font-weight:lighter;
}

.scoreboard {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: center;
  margin: 2.5% 0 -2.5%;
  letter-spacing: 0;
}

.menu {
  width: 80%;
  flex: 1 1;
  font-size: 1.2em;
}

.list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.buttonContainer {
  background-color: grey;
  padding: 10px 50px;
  border: 1px solid black;
  border-radius: 10px;
}

.buttonContainer:hover {
  background-color: rgb(207, 207, 207);
  color: #5c677d;
  font-weight: bold;
}

.logos {
  margin: 50px auto;
  width: 50%;
}

p {
  font-weight: bold;
  color: #17408B;
}
nav {
}

.nav_Style {
    color: white;
    font-weight: bold;
    margin: 25px;
}

.nav_ul {
    height: 10vh;
    list-style: None;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: grey;
    background-image:
    linear-gradient(
        45deg, 
        #17408B 13%,
        grey 13%,
        grey 87%,
        #C9082A 87%
    );
}

.first_item {
    margin-right: auto;
    font-size: 150%;
}


.two:hover ~ hr {
    margin-left: 25%;
  }
  
.three:hover ~ hr {
    margin-left: 50%;
  }
  
.four:hover ~ hr {
    margin-left: 75%;
  }

hr {
    height: .25rem;
    width: 25%;
    margin: 0;
    background: #17408B;
    border: none;
    transition: .3s ease-in-out;
  }

.nav_item:hover:after {
    width: 100%;
    transition: width 150ms ease-in;
  }
.games {
    min-height: 77vh;
}

.container {
    margin:1em;
    height:205px;
    display:flex;
    align-items:center;
    font-size:1.1em;
  }

.text {
    letter-spacing:0;
    text-align:center;
    padding:0.8em 1.6em;
    border:1px solid currentColor;
    border-radius:2px;
    background:grey;
    text-transform:capitalize;
    font-weight:lighter;
    color:white;
    font-weight: bold;
  }
.calendarDates {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    color: #5c677d;
    margin: 25px;
  }

.calStyle {
    color: #5c677d;
    font-weight: bold;
}
  
.cal-links{
    width: 80%;
    display: flex;
    justify-content: space-around;
    list-style: None;
    margin: auto;
    text-align: right;
    background: white;
    padding: 10px;
  }

.currentDate {
  font-size: 200%;
}

.otherDates {
  font-size: 175%;
}

.otherDatesBefore {
  font-size: 150%;
}
.players {
    min-height: 77vh;
}

table {
    border-collapse: collapse;
    margin-left: auto;
    margin-right: auto;
    border: 3px solid black;
    width: 100%;
  }
td, th {
    padding: .5rem;
    text-align: center;
    border: 1px solid;
  }

.flex_container {
    display: flex;
    flex-direction: column;
    width: 70%;
    flex-wrap: wrap;
    margin: auto;
    padding: 0.5rem 0rem;
}

.playerInfo {
    text-align: left;
}

.playerButton {
    margin-left: auto;
    background-color: #C9082A;
    border: none;
    color: white;
    font-weight: bold;
    padding: 0.3em;
}

table {
    table-layout: fixed;
}

.tables_container {
    float: center;
    width: 100%;
}

.playerName {
    font-size: 200%;
}

.flex_horizontal {
    display:flex;
    align-content: center;
    align-items: center;
}
.footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background: #D3D3D3;
    color: #5c677d;
    font-weight: bold;
  }

.footerStyle {
    color: #5c677d;
    font-weight: bold;
}
  
.footer-links{
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: right;
    list-style: None;
  }
.App {
  text-align: center;
  font-family: 'Alegreya';
  height: 100%;
  background-color: #E8E8E8;
  background-image:
    linear-gradient(
      45deg, 
      #EEEEEE 20%,
      #CCCCCC 20%,
      #CCCCCC 40%,
      #999999 40%,
      #999999 60%,
      #666666 60%,
      #666666 80%,
      #333333 80%
    );
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
